.title {
	font-size: 30px;
	margin-bottom: 8px;
}

.text {
	font-family: 'Approach';
	font-weight: 400;
	font-size: 18px;
}


* {
  box-sizing: border-box;
  scrollbar-width: none;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  display: none;
  -webkit-appearance: none;
}

html, body {
	height: 100%;
}

img {
	user-drag: none;
	user-select: none;
	-moz-user-select: none;
	-webkit-user-drag: none;
	-webkit-user-select: none;
	-ms-user-select: none;
}

.page {
	background-image: radial-gradient(#1f1f1f, #000000);
	
	&__inner {
		padding: 32px;
		min-height: 100vh;
		height: 100%;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		position: relative;
	}

}



.contact-card {
    width:100%;
		max-width: 230px;
		aspect-ratio: 1 / 1.5;
		
		&__inner {
			transform: rotate(15deg) perspective(2000px);
			height: 100%;
			position: relative;
			transform-style: preserve-3d;
		}

		
		&__side {
			position: absolute;
			top:0;
			left:0;
			height:100%;
			width:100%;
			background: $black;
			-webkit-backface-visibility: hidden;
			backface-visibility: hidden;
			padding: 24px;
			margin-bottom: 0;
			transform-origin: 50% 50% 0;
			transform-style: preserve-3d;
			
			
			&--front { transform: translateZ(0px); }
			&--back { transform: translateZ(-5px) rotateY(180deg); }
		
		}
		
		&__logo {
				margin-top: 25px;
				width: 100%;
				margin-bottom: 8px;
			}

		&__title {
	color: $white;
	font-size: 16.3px;
	text-align: center;
}

	&__address-item {
	color: $white;
	text-decoration: none;
	font-size: 11.5px;
	line-height: 1.2;
	display: block;
	font-weight: 700;
	font-family: "Approach Mono";
}


&__thick {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	height: 100%;
	z-index: -10;
	transform-origin: 0% 0%;
	transform: rotateY(90deg);
	background-color: white;
	
	&--left {
		height: 100%;
		width: 2.5px;
		top: 0;
		bottom: 0;
		left: 0;
	}
	
	&--right {
		height: 100%;
		width: 2.5px;
		top: 0;
		bottom: 0;
		right: -2.5px;
	}
	
	&--top {
		transform: rotateX(-90deg);
		right: 0;
		height: 2.5px;
		width: 100%;
		right: 0;
	}
	
	&--bottom {
		transform: rotateX(-90deg);
		right: 0;
		height: 2.5px;
		width: 100%;
		right: 0;
		top: unset;
		bottom: -2.5px;
	}
	
}
}

	@media (max-width: 400px) {
		
		.contact-card {
			max-width: 200px;
			width: 200px;

			&__address-item {
				font-size: 9.5px;
			}

			&__title {
				font-size: 13.4px;
			}

		}

	}






















